<template>
  <div>
    <v-lazy
      class="fix_box"
      v-model="isActive"
      :options="{
        threshold: 0.5,
      }"
      min-height="200"
      transition="fade-transition"
    >
      <div class="list_coin">
        <div class="coin depressed" v-for="item in listCoin" :key="item.id">
          <img :src="item.ImageUrl" alt="" />
        </div>
      </div>
    </v-lazy>
    <div class="q_area">
      <v-autocomplete
        single-line
        class="w120"
        :items="coinData.FullName"
        item-text="name"
        item-value="name"
        label="XRP"
        solo
        v-model="sendDataKr.fsym.name"
      ></v-autocomplete>
      <div class="wrap_input">
        <h3 class="label_list">최초거래</h3>
        <v-select
          v-model="period"
          @change="changePeriod"
          :hint="`${period.state}`"
          :items="periodItems"
          item-text="state"
          item-value="value"
          label="3년"
          persistent-hint
          return-object
          single-line
        ></v-select>
      </div>
      <div class="wrap_input">
        <v-radio-group v-model="q1">
          <template v-slot:label>
            <h3 class="label_list">변동성 기준</h3>
          </template>
          <v-radio value="maxChangCoin">
            <template v-slot:label>
              <div>하루 동안 변동성이 큰 코인</div>
            </template>
          </v-radio>
          <v-radio value="minChangCoin">
            <template v-slot:label>
              <div>하루 동안 변동성이 작은 코인</div>
            </template>
          </v-radio>
          <v-btn @click="actionQ1">확인</v-btn>
        </v-radio-group>
      </div>
      <div class="wrap_input">
        <v-radio-group v-model="q2">
          <template v-slot:label>
            <h3 class="label_list">시총 기준</h3>
          </template>
          <v-radio value="maxCapCoin">
            <template v-slot:label>
              <div>시총이 큰 코인</div>
            </template>
          </v-radio>
          <v-radio value="minCapCoin">
            <template v-slot:label>
              <div>시총이 작은 코인</div>
            </template>
          </v-radio>
          <v-btn @click="actionQ2">확인</v-btn>
        </v-radio-group>
      </div>
      <div class="wrap_input">
        <v-radio-group v-model="q3">
          <template v-slot:label>
            <h3 class="label_list">거래량 기준</h3>
          </template>
          <v-radio value="maxVolumCoin">
            <template v-slot:label>
              <div>거래량 큰 코인</div>
            </template>
          </v-radio>
          <v-radio value="minVolumCoin">
            <template v-slot:label>
              <div>거래량 큰 작은 코인</div>
            </template>
          </v-radio>
          <v-btn @click="actionQ3">확인</v-btn>
        </v-radio-group>
      </div>
      <div class="wrap_input">
        <v-radio-group v-model="q4">
          <template v-slot:label>
            <h3 class="label_list">평가 기준</h3>
          </template>
          <v-radio value="pass">
            <template v-slot:label>
              <div>평가 C- 포함</div>
            </template>
          </v-radio>
          <v-radio value="minRateCoin">
            <template v-slot:label>
              <div>평가 C- 제외</div>
            </template>
          </v-radio>
          <v-btn @click="actionQ4">확인</v-btn>
        </v-radio-group>
      </div>
    </div>

    <v-btn @click="add01('xrp', 0)">xrp</v-btn>
    <v-btn @click="add01('btc', 1)">btc</v-btn>
    <v-btn @click="add01('ETH', 2)">ETH</v-btn>
    <v-btn @click="add00">합치기</v-btn>
  </div>
</template>
<script>
// import { JSONParser } from '@amcharts/amcharts4/core';
// import { defineComponent } from '@vue/composition-api'
import axios from "axios";
import deepmerge from "deepmerge";
export default {
  data() {
    return {
      isActive: false,
      //코인 리스트
      coinData: {},
      loveCoin:{},
      listCoin: {},
      resultCoinPriod: {},
      resultCoinQ1: {},
      resultCoinQ2: {},
      resultCoinQ3: {},
      resultCoinQ4: {},
      period: { state: "기간", value: "20" },
      periodItems: [
        // { state: "3년", value: 20 },
        // { state: "1년", value: 7 },
        // { state: "1개월", value: 1 },
        {
          state: "3년",
          value: new Date().setFullYear(new Date().getFullYear() - 3) / 1000,
        },
        {
          state: "1년",
          value: new Date().setFullYear(new Date().getFullYear() - 1) / 1000,
        },
        {
          state: "1개월",
          value: new Date().setMonth(new Date().getMonth() - 1) / 1000,
        },
      ],
      q1: {},
      q2: {},
      q3: {},
      q4: {},
      select1: {},
      select2: {},
      addData: {},
      sendDataKr: {
        duration: "histominute",
        fsym: { name: "XRP" },
        tsym: "KRW",
        limit: "50",
        Apikey:
          "bd1c741f37bd77cd9f5554daed59741b3a75101cf1b5437be864fb4e4bafb0d6",
      },
      chartData: [],

      dataBefore: [],
      raceData: [],
    };
  },
  mounted() {},
  created() {
    document.cookie = "crossCookie=_ga; SameSite=None; Secure";
    document.cookie = "crossCookie=_gid; SameSite=None; Secure";

    this.getCoinData();
    //  this.chart1("klay");
    // this.chart1("xrp");
    // .then(this.chart1("xrp");
    // .merge(  this.chart1("klay"),  this.chart1("xrp"))
    // console.log(this.chartData)
    //  console.log('after', this.chart1("klay"))
    //  console.log( this.chart1("xrp"))
    //  console.log( "ladish",_.merge(  this.chart1("klay"),  this.chart1("xrp")))
  },
  methods: {
    actionQ1() {
      switch (this.q1) {
        case "maxChangCoin": {
          const resultQ1 = JSON.parse(JSON.stringify(this.resultCoinPriod));
          // console.log("maxChangCoin", this.resultCoinPriod);
          resultQ1.sort(function (a, b) {
            return b["CHANGEDAY"] - a["CHANGEDAY"];
          });
          // console.log(resultQ1);
          if (resultQ1.length > 60) {
            resultQ1.splice(60, resultQ1.length);
          }
          this.listCoin = resultQ1;
          this.resultCoinQ1 = resultQ1;
          break;
        }
        case "minChangCoin": {
          const resultQ1 = JSON.parse(JSON.stringify(this.resultCoinPriod));
          // console.log("minChangCoin", this.resultCoinPriod);
          resultQ1.sort(function (a, b) {
            return a["CHANGEDAY"] - b["CHANGEDAY"];
          });
          // console.log(resultQ1);
          if (resultQ1.length > 60) {
            resultQ1.splice(60, resultQ1.length);
          }
          this.listCoin = resultQ1;
          this.resultCoinQ1 = resultQ1;
          break;
        }

        default: {
          // console.log("Invalid choice");
          break;
        }
      }
    },
    actionQ2() {
      switch (this.q2) {
        case "maxCapCoin": {
          const resultQ2 = JSON.parse(JSON.stringify(this.resultCoinQ1));
          // console.log(resultQ2);
          resultQ2.sort(function (a, b) {
            return b["MKTCAP"] - a["MKTCAP"];
          });
          // console.log(resultQ2);
          if (resultQ2.length > 40) {
            resultQ2.splice(40, resultQ2.length);
          }
          this.listCoin = resultQ2;
          this.resultCoinQ2 = resultQ2;
          break;
        }
        case "minCapCoin": {
          const resultQ2 = JSON.parse(JSON.stringify(this.resultCoinQ1));
          // console.log("B", resultQ2);
          resultQ2.sort(function (a, b) {
            return b["MKTCAP"] - a["VOLUME24HOUR"];
          });
          // console.log("resultQ2", resultQ2);
          if (resultQ2.length > 40) {
            resultQ2.splice(40, resultQ2.length);
          }
          this.listCoin = resultQ2;
          this.resultCoinQ2 = resultQ2;
          break;
        }

        default: {
          // console.log("Invalid choice");
          break;
        }
      }
    },

    actionQ3() {
      switch (this.q3) {
        case "maxVolumCoin": {
          const resultQ3 = JSON.parse(JSON.stringify(this.resultCoinQ2));
          console.log(resultQ3);
          resultQ3.sort(function (a, b) {
            return b["VOLUME24HOUR"] - a["VOLUME24HOUR"];
          });
          console.log(resultQ3);
          if (resultQ3.length > 20) {
            resultQ3.splice(20, resultQ3.length);
          }
          this.listCoin = resultQ3;
          this.resultCoinQ3 = resultQ3;
          break;
        }
        case "minVolumCoin": {
          const resultQ3 = JSON.parse(JSON.stringify(this.resultCoinQ2));
          console.log(resultQ3);
          resultQ3.sort(function (a, b) {
            return a["VOLUME24HOUR"] - b["VOLUME24HOUR"];
          });
          console.log("resultQ3", resultQ3);
          if (resultQ3.length > 20) {
            resultQ3.splice(20, resultQ3.length);
          }
          this.listCoin = resultQ3;
          this.resultQ3 = resultQ3;

          break;
        }

        default: {
          // console.log("Invalid choice");
          break;
        }
      }
    },
    actionQ4() {
      switch (this.q4) {
        case "pass": {
          const resultQ4 = JSON.parse(JSON.stringify(this.resultCoinQ3));
          console.log("resultQ4", resultQ4);
          this.listCoin = resultQ4;
          this.resultQ4 = resultQ4;
          break;
        }
        case "minRateCoin": {
          let resultQ4 = JSON.parse(JSON.stringify(this.resultCoinQ3)).filter(
            (it) => it.Rating == !"c-"
          );
          console.log(resultQ4);

          console.log("resultQ4", resultQ4);
          if (resultQ4.length > 10) {
            resultQ4.splice(10, resultQ4.length);
          }
          this.listCoin = resultQ4;
          break;
        }

        default: {
          // console.log("Invalid choice");
          break;
        }
      }
    },
    // 기간선택
    changePeriod() {
      let res = this.coinData.filter(
        (it) => it.AssetLaunchDate < this.period.value
      );
      this.listCoin = res;
      this.resultCoinPriod = res;
    },

    // 초기 코인리스트
    async getCoinData() {
      try {
        // let duration = this.sendDataKr.duration;
        let responseCoinList = await axios.get(
          `https://min-api.cryptocompare.com/data/top/totalvolfull`,
          {
            params: {
              // Apikey: this.sendDataKr.Apikey,
              // limit: this.sendDataKr.limit,
              tsym: "krw",
              limit: 100,
            },
          }
        );
        console.log(responseCoinList);
        let getCoinData = [];
        for (let i = 0; i < responseCoinList.data.Data.length; i++) {
          getCoinData.push({
            Name: responseCoinList.data.Data[i].CoinInfo.Name,
            AssetLaunchDate:
              new Date(
                responseCoinList.data.Data[i].CoinInfo.AssetLaunchDate
              ).getTime() / 1000,
            Id: responseCoinList.data.Data[i].CoinInfo.Id,
            FullName: responseCoinList.data.Data[i].CoinInfo.FullName,
            Internal: responseCoinList.data.Data[i].CoinInfo.Internal,
            CHANGEDAY: responseCoinList.data.Data[i].RAW.KRW.CHANGEDAY,
            VOLUME24HOUR: responseCoinList.data.Data[i].RAW.KRW.VOLUME24HOUR,
            MKTCAP: responseCoinList.data.Data[i].RAW.KRW.MKTCAP,
            Rating: responseCoinList.data.Data[i].CoinInfo.Rating.Weiss.Rating,
            ImageUrl:
              "https://www.cryptocompare.com/" +
              responseCoinList.data.Data[i].CoinInfo.ImageUrl +
              "?width=55",
          });
        }
        this.coinData = getCoinData;
        this.listCoin = getCoinData;
        console.log(responseCoinList);
        this.resultCoinPriod = getCoinData;
        this.resultCoinQ1 = getCoinData;
        this.resultCoinQ2 = getCoinData;
        this.resultCoinQ3 = getCoinData;
        this.resultCoinQ4 = getCoinData;
      } catch (error) {
        // console.log(Object.keys(error), error.message);
      }
    },
    add01(abc, idx) {
      this.chart1(abc).then((value) => (this.select1[idx] = value));
      //  console.log(this.chart1("xrp");
      console.log(this.select1);
    },
    add00() {
      this.addData = deepmerge(
        deepmerge(this.select1[0], this.select1[1]),
        this.select1[2]
      );
    },
    async chart1(coinName) {
      try {
        // let duration = this.sendDataKr.duration;
        let responseChart = await axios.get(
          `https://min-api.cryptocompare.com/data/v2/histoday`,
          {
            params: {
              fsym: coinName,
              // Apikey: this.sendDataKr.Apikey,
              limit: this.sendDataKr.limit,
              tsym: this.sendDataKr.tsym,
              aggregate: 20,
            },
          }
        );

        let getChartData = [];
        for (let i = 0; i < responseChart.data.Data.Data.length; i++) {
          getChartData.push({
            eq: 1000 / responseChart.data.Data.Data[0].open,
            name: coinName,
            open: responseChart.data.Data.Data[i].open,
            low: responseChart.data.Data.Data[i].low,
            high: responseChart.data.Data.Data[i].high,
            close: responseChart.data.Data.Data[i].close,
            time: responseChart.data.Data.Data[i].time,
            volume: responseChart.data.Data.Data[i].volumeto,
            value: responseChart.data.Data.Data[i].open,
          });
        }
        let addData = {};
        for (let i = 0; i < getChartData.length; i++) {
          const key = getChartData[i].time;
          addData[key] = [
            {
              name: getChartData[i].name,
              open: getChartData[i].open,
              amount: getChartData[0].eq * getChartData[i].open,
            },
          ];
        }
        console.log("addData", addData);
        return addData;
        // let data011 =   _.merge(  this.dataBefore, addData)
        // this.dataBefore.push(addData);

        // console.log("dataBefore", this.dataBefore);
        // .push(dataBefore);
        // this.chartData = _.merge(this.raceData, aaa);
        // console.log(this.chartData);
      } catch (error) {
        // console.log(Object.keys(error), error.message);
      }
    },
    test00() {
      const beforeData = [
        {
          close: 1214.06,
          conversionSymbol: "",
          conversionType: "direct",
          high: 1214.65,
          low: 1211.11,
          open: 1212.09,
          time: 1621928400,
          volumefrom: 435303.84,
          volumeto: 528084684.33,
          name: "AA",
        },
        {
          close: 1214.06,
          conversionSymbol: "",
          conversionType: "direct",
          high: 1214.65,
          low: 1211.11,
          open: 1212.09,
          time: 1621928401,
          volumefrom: 435303.84,
          volumeto: 528084684.33,
          name: "BB",
        },
      ];

      let afterData = {
        1621928400: [
          {
            name: "BB",
            high: 1214.65,
          },
        ],
        1621928401: [
          {
            name: "BB",
            high: 1214.65,
          },
        ],
      };
      console.log("받아오는 데이터", beforeData);

      console.log("원하는 변환", afterData);

      let aaa = {};
      console.log(this.chartData);
      for (let i = 0; i < this.chartData.length; i++) {
        console.log(this.chartData[i].time);
        const key = this.chartData[i].time;
        aaa[key] = [{ name: this.chartData[i].name, high: beforeData[i].high }];
      }
      return "ysk";
      // console.log("aaa", aaa);
    },
  },
};
</script>
